import React, {useEffect, useRef} from 'react'
import * as echarts from 'echarts/core'
import {LineChart, BarChart, LineSeriesOption, BarSeriesOption} from "echarts/charts";
import {
    TitleComponent,
    TitleComponentOption,
    GridComponent,
    GridComponentOption,
    TooltipComponent,
    TooltipComponentOption,
    LegendComponent,
    LegendComponentOption
} from "echarts/components";
import {CanvasRenderer} from "echarts/renderers";
import {HiDownload} from 'react-icons/hi'

import {downloadPng} from "../utils/download";

interface BarLineChartProps {
    width?: string;
    height?: string;
    title?: string;
}

type ECOption = echarts.ComposeOption<BarSeriesOption | LineSeriesOption | TitleComponentOption | GridComponentOption | TooltipComponentOption | LegendComponentOption>;

echarts.use([LineChart, BarChart, TitleComponent, GridComponent, TooltipComponent, LegendComponent, CanvasRenderer])

function StockChart(props: BarLineChartProps) {
    const chartRef = useRef<HTMLDivElement | null>(null)
    const {width = '100%', height = '400px', title = 'Title'} = props;
    const option: ECOption = {
        title: {
            text: title,
            textStyle: {
                fontSize: 24
            }
        },
        grid: {
            backgroundColor: 'white',
            top: 70,
            right: 50,
            left: 50
        },
        legend: {
            data: ['Out of Stock', 'Low Stock', 'High Stock', 'Available, Stock Unknown'],
            bottom: 0
        },
        xAxis: [
            {
                type: 'category',
                boundaryGap: false,
                data: ['2021/07/01', '2021/07/02', '2021/07/03', '2021/07/04', '2021/07/05', '2021/07/06']
            }
        ],
        yAxis: [
            {
                type: 'value',
            },
        ],
        series: [
            {
                type: 'line',
                name: 'Out of Stock',
                stack: 'stack',
                data: [10, 20, 30, 40, 50, 60],
                areaStyle: {}
            },
            {
                type: 'line',
                name: 'Low Stock',
                stack: 'stack',
                data: [10, 10, 0, 20, 0, 5],
                areaStyle: {}
            },
            {
                type: 'line',
                name: 'High Stock',
                stack: 'stack',
                data: [20, 70, 0, 20, 26, 15],
                areaStyle: {}
            },
            {
                type: 'line',
                name: 'Available, Stock Unknown',
                stack: 'stack',
                data: [60, 0, 70, 20, 24, 20],
                areaStyle: {}
            }
        ]
    }

    useEffect(() => {
        if (!chartRef.current) return

        const chart = echarts.init(chartRef.current, 'customed')
        chart.setOption(option)
    })

    const onDownload = async () => {
        if (!chartRef.current) return

        await downloadPng(chartRef.current, title)
    }

    return (
        <div className="relative">
            <div ref={chartRef} style={{width, height}}/>
            <HiDownload className="absolute right-0 top-0 text-3xl cursor-pointer" onClick={onDownload}/>
        </div>
    )
}

export default StockChart
