import request from './https'
import { AxiosPromise } from "axios";
import {ProductBarChart, SearchParams, TopBrandBarChart, TopBrandPieChart} from "../types/market";

export function getTopBrandBarChart(data: SearchParams) {
    return request({
        url:'/market/get_top_brand_bar_chart',
        method: 'GET',
        params: data
    }) as AxiosPromise<TopBrandBarChart>
}

export function getTopBrandPieChart(data: SearchParams) {
    return request({
        url:'/market/get_top_brand_pie_chart',
        method: 'GET',
        params: data
    }) as AxiosPromise<TopBrandPieChart>
}

export function getTopProductBarChart(data: SearchParams) {
    return request({
        url:'/market/get_top_product_bar_chart',
        method: 'GET',
        params: data
    }) as AxiosPromise<ProductBarChart>
}
